<template>
  <div class="category">
    <!-- 分类 -->
    <van-nav-bar title="全部分类" fixed />

    <!-- 搜索框 -->
    <van-search
      readonly
      shape="round"
      background="#f1f1f2"
      placeholder="请输入搜索关键词"
      @click="$router.push('/search')"
    />

    <!-- 分类列表 -->
    <div class="list-box">
      <div class="left">
        <ul>
          <li v-for="(item, index) in list" :key="item.category_id">
            <a :class="{ active: index === activeIndex }" @click="activeIndex = index" href="javascript:;">{{ item.name }}</a>
          </li>
        </ul>
      </div>
      <div class="right">
        <div @click="$router.push(`/searchlist?categoryId=${item.category_id}`)" v-for="item in list[activeIndex]?.children" :key="item.category_id" class="cate-goods">
          <img :src="item.image?.external_url" alt="">
          <p>{{ item.name }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getCategoryData } from '@/api/category'
export default {
  name: 'CategoryPage',
  created () {
    this.getCategoryList()
  },
  data () {
    return {
      list: [],
      activeIndex: 0
    }
  },
  methods: {
    async getCategoryList () {
      const { data: { list } } = await getCategoryData()
      this.list = list
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.category {
  padding-top: 100px; // 顶部留出导航栏和搜索框的空间
  padding-bottom: 50px; // 底部留白
  height: 100vh; // 占据整个视口高度
  .list-box {
    height: 100%; // 继承父容器高度
    display: flex; // 启用弹性布局
    .left {
      width: 85px; // 固定左侧宽度
      height: 100%; // 继承父容器高度
      background-color: #f3f3f3; // 浅灰背景
      overflow: auto; // 允许纵向滚动
      a {
        display: block; // 块级元素
        height: 45px; // 固定高度
        line-height: 45px; // 垂直居中
        text-align: center; // 水平居中
        color: #444444; // 文字颜色
        font-size: 12px; // 小号字体
        &.active { // 被选中的分类样式
          color: #fb442f; // 主题红色
          background-color: #fff; // 白色背景
        }
      }
    }
    .right {
      flex: 1; // 占据剩余空间
      height: 100%;
      background-color: #ffffff;
      display: flex; // 弹性布局
      flex-wrap: wrap; // 允许换行
      justify-content: flex-start; // 左对齐
      align-content: flex-start; // 顶部对齐
      // align-items: center;
      padding: 10px 0; // 上下内边距
      overflow: auto; // 允许滚动

      .cate-goods {
        width: 33.3%; // 每行显示3个
        margin-bottom: 10px; // 底部间距
        img {
          width: 70px; // 固定图片尺寸
          height: 70px;
          display: block; // 防止图片间距
          margin: 5px auto; // 水平居中+上下间距
        }
        p {
          text-align: center; // 文字居中
          font-size: 12px; // 小号字体
        }
      }
    }
  }
}

// 导航条样式定制
.van-nav-bar {
  z-index: 999; // 确保导航在最顶层
}

// 搜索框样式定制
.van-search {
  position: fixed; // 固定定位
  width: 100%; // 全宽
  top: 46px; // 位于导航栏下方
  z-index: 999; // 确保层级
}
</style>
